    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
}

.icon-search {
    font-size: 1.2rem;
}

.search__input {
    width: 100%;
    margin-left: 1ch;
    cursor: text;
    line-height: 2rem;
    background-color: var(--primary-dark-color);
    border: none;
    border-bottom: 1px solid var(--input-color);
    color: var(--input-color);
    -webkit-animation: fadeIn .5s ease;
            animation: fadeIn .5s ease;
