.carousel__items--big {
	--item-width: 120px;
	--item-height: calc(var(--item-width) * 63 / 42);
}

.carousel__items--default {
	--item-width: 160px;
	--item-height: calc(var(--item-width) * 14 / 19);;
}

.carousel {
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
}

.carousel__icon {
	display: none;
}

.carousel__container--outer {
    overflow: scroll;
}

.carousel__container--outer::-webkit-scrollbar {
    display: none;
}

.carousel__container--inner {
    display: -webkit-flex;
    display: flex;
	-webkit-flex-wrap: nowrap;
	        flex-wrap: nowrap;
    transition: var(--transition-time);
    padding: 2rem 0;
}

.carousel__item {
	position: relative;
	width: var(--item-width);
	height: var(--item-height);
	margin-right: 1ch;
	cursor: pointer;
	-webkit-transform-origin: center left;
	        transform-origin: center left;
	transition: var(--transition-time);
}

.carousel__item--img {
	width: var(--item-width);
    height: var(--item-height);
}

.carousel__item--details {
    text-align: center;
	position: absolute;
	bottom: 0;
	left: 0;
	right: 0;
    top: 0;
    color: var(--text-color);
	font-weight: 700;
    padding: 1ch;
    margin: 0;
	background: linear-gradient(to bottom, rgba(0,0,0,0.9) 0%, rgba(0,0,0,0) 100%);
}


@media (min-width: 768px) {
	.carousel__items--big {
		--transition-time: 500ms;
		--item-width: 168px;
		--item-height: calc(var(--item-width) * 63 / 42);
		--grow-factor: 1.2;
		--move-right: 40px;
		--move-left:  -20px;
	}

	.carousel__items--default {
		--transition-time: 500ms;
        --item-width: 190px;
		--item-height: calc(var(--item-width) * 14 / 19);
		--grow-factor: 1.2;
		--move-right: 55px;
		--move-left: -20px;
	}

    .carousel__icon {
        cursor: pointer;
        font-size: 1.3rem;
        display: block;
        margin: 1ch;
    }

    .carousel__container--outer {
        scroll-behavior: smooth;
    }

    .carousel__item--details {
        opacity: 0;
    }

	.carousel__container--inner:hover {
		-webkit-transform: translate3d(var(--move-left), 0, 0);
		        transform: translate3d(var(--move-left), 0, 0);
	}

	.carousel__item:hover ~ .carousel__item {
		-webkit-transform: translate3d(var(--move-right), 0, 0);
		        transform: translate3d(var(--move-right), 0, 0);
	}

	.carousel__container--inner:hover .carousel__item {
		opacity: 0.3;
	}

	.carousel__container--inner:hover .carousel__item:hover {
		-webkit-transform: scale(var(--grow-factor));
		        transform: scale(var(--grow-factor));
		opacity: 1;
	}

	.carousel__item:hover .carousel__item--details {
		opacity: 1;
    }
}

:root {
  --primary-dark-color: #212121;
  --primary-color: #424242 ;
  --accent-color: #E50914;
  --text-color: #e0e0e0;
  --input-color: #e0e0e0;
  --title-color: #f5f5f5;
  --link-color: #f5f5f5;
}

body {
  background-color: #424242;
  background-color: var(--primary-color);
  color: #e0e0e0;
  color: var(--text-color);
  font-family: 'Helvetica Neue', Helvetica, sans-serif;
  font-size: 14px;
}

header {
  padding: 1rem 2rem;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  height: 40px;
  background-color: #212121;
  background-color: var(--primary-dark-color);
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  -webkit-align-items: center;
          align-items: center;
  z-index: 1;
}

.header__logo {
  height: 40px;
  width: 100px;
  background: url(/marvel-characters-react/static/media/marvel-logo.77a0b8d8.png);
  background-position: center;
  background-repeat: no-repeat;
  background-size: contain;
}

main {
  width: 90%;
  margin: 6rem auto;
}

h1 {
  font-weight: bolder;
  color: #f5f5f5;
  color: var(--title-color);
  font-size: 2rem;
}

h2 {
  font-size: 1.5rem;
  font-weight: bolder;
  color: #f5f5f5;
  color: var(--title-color);
}

footer {
  padding: .5rem 0;
  width: 100%;
  text-align: center;
}

ul {
  padding: 0px;
  margin: 0px;
  list-style-type: none;
}

@media (min-width: 768px){
  body {
    font-size: 16px;
  }
  h1 {
    font-size: 2rem;
    margin-top: 0;
    margin-bottom: 1rem;
  }
  main {
    width: 80%;
  }
}

@media (min-width: 992px){
  main {
    width: 60%;
  }
}

