@font-face {
  font-family: 'icomoon';
  src:  url('fonts/icomoon.eot?xvaxpa');
  src:  url('fonts/icomoon.eot?xvaxpa#iefix') format('embedded-opentype'),
    url('fonts/icomoon.ttf?xvaxpa') format('truetype'),
    url('fonts/icomoon.woff?xvaxpa') format('woff'),
    url('fonts/icomoon.svg?xvaxpa#icomoon') format('svg');
  font-weight: normal;
  font-style: normal;
}

[class^="icon-"], [class*=" icon-"] {
  /* use !important to prevent issues with browser extensions that change fonts */
  font-family: 'icomoon' !important;
  speak: none;
  font-style: normal;
  font-weight: normal;
  font-variant: normal;
  text-transform: none;
  line-height: 1;

  /* Better Font Rendering =========== */
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.icon-circle-o-notch:before {
  content: "\f1ce";
}
.icon-search:before {
  content: "\f002";
}
.icon-chevron-left:before {
  content: "\f053";
}
.icon-chevron-right:before {
  content: "\f054";
}
